<template>
  <div class="cube">
      <div class="plane" :style="style">{{tip}}</div>
  </div>
</template>

<script>
export default {
    data(){
        return {
            
        }
    },
    computed:{
        tip: function(){
            return "CSS 3D"
        },
        style: function(){
            var list = [];
            var styles = this.$store.state.styles;
            var str;
            for(var i = 0; i < styles.length; i++){
                var key = styles[i];
               if(key.substr(0, 1) === "r"){
                   str = `${key}(${this.$store.state[key]}deg)`;
               }
               else{
                    str = `${key}(${this.$store.state[key]}px)`;
               }
               list.push(str);
            }
            var ts = list.join("");
            return {
                transform: ts
            }
        }
    }
}


</script>

<style lang="stylus">
.cube{
    transform-style preserve-3d
    perspective 900px
    position absolute
    width 200px
    height 200px
}
.plane{
    position absolute
    left 0
    top 0
    width 200px
    height 200px
    display flex
    font-size 40px
    background rgba(0, 0, 0, 0.7)
    justify-content center
    align-items center
    color #ffffff
}
</style>




