import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    rotateX:0,
    rotateY:0,
    rotateZ:0,
    translateX:0,
    translateY:0,
    translateZ:0,
    styles:["rotateX", "rotateY", "rotateZ", "translateX", "translateY", "translateZ"],
    zIndex:1
}

const mutations = {
    change(state, list){
        var key = list[0];
        var data = list[1];
        state[key] = data;
    },
    sort(state, list){
        state.styles = list;
    },
    updateIndex(state){
        state.zIndex++;
    }
}

export default new Vuex.Store({
    state,mutations
})


